.gallery-preview-section {
   background-color: #f2f2f2;
   display:  flex;
   flex-direction: column;
   align-items: center;
}

.gallery-preview-healine {
    width: 60dvw;
    text-align: center;
    margin: 3rem 0rem;
    font-size: 48px;
    color: var(--primary);
}


.preview-gallery-cta{
    margin: 2.5rem 0rem;
}


.right-arrow-icon {
    height: 17px;
    width: 17px;
}

@media only screen and (max-width: 1024px ) {
    .gallery-preview-healine {
        width: 80dvw;
    }
}


@media only screen and (max-width: 768px ) {
    .gallery-preview-healine {
        width: 90dvw;
    }
}